<script setup lang="ts">
import {ref, nextTick, computed} from 'vue'
import { Search } from '@element-plus/icons-vue'
import {editRule, getUserRule} from "@/api/api.ts";
import {addRule} from "@/api/api.ts";
import {ElMessage} from "element-plus";
const visible = ref(false);
const loading = ref(false);
const formRef = ref(null);
const radioKey = ref(0);

// 表单数据
const form = ref({
  id: 0,
  title: '',
  rule: '',
  parent_id: '', // 默认选中"顶级菜单"
  is_menu: 0,
  weight: 0,
  status: 1,
  remark: '',
  path: '',
  icon: ''
})

// 表单验证规则
const rules = ref({
  title: [
    { required: true, message: '请输入权限名称', trigger: 'blur' },
    { min: 2, max: 50, message: '长度在 2 到 50 个字符', trigger: 'blur' }
  ],
  rule: [
    { required: true, message: '请输入权限标识', trigger: 'blur' },
    { pattern: /^[a-zA-Z0-9:_-]+$/, message: '权限标识只能包含字母、数字、冒号、下划线和横线', trigger: 'blur' }
  ],
  path: [
    { 
      validator: (_rule: any, value: any, callback: any) => {
        if (form.value.is_menu === 1 && !value) {
          callback(new Error('菜单类型必须填写前端路径'));
        } else {
          callback();
        }
      }, 
      trigger: 'blur' 
    }
  ]
});

const treeData = ref([])

// 使用computed属性处理is_menu的响应式
const isMenuValue = computed({
  get: () => {
    console.log('获取is_menu值:', form.value.is_menu, typeof form.value.is_menu);
    return form.value.is_menu;
  },
  set: (value) => {
    console.log('设置is_menu值:', value, typeof value);
    form.value.is_menu = value;
    // 触发菜单类型变化处理
    handleMenuChange(value);
  }
});

const GetUserRule = async () => {
  try {
    console.log('开始获取权限树数据...');
    // 传递当前编辑的ID，如果是新增则为0
    const response = await getUserRule(form.value.id || 0);
    console.log('权限树数据响应:', response);
    
    if (response && response.data) {
      treeData.value = response.data || [];
      console.log('权限树数据设置成功:', treeData.value);
    } else {
      treeData.value = [];
      console.log('权限树数据为空，设置为空数组');
    }
  } catch (error) {
    console.error('获取用户权限失败:', error);
    treeData.value = [];
  }
}

// 处理菜单类型变化
const handleMenuChange = (value: any) => {
  console.log('菜单类型变化:', value, typeof value);
  
  if (value === 0) {
    // 如果不是菜单，清空路径和图标
    form.value.path = '';
    form.value.icon = '';
    console.log('已清空路径和图标');
  }
}

// 显示图标帮助
const showIconHelp = () => {
  ElMessage.info('常用图标：fa fa-home（首页）、fa fa-user（用户）、fa fa-cog（设置）、fa fa-list（列表）');
}

// 处理对话框关闭
const handleClose = (done: () => void) => {
  if (loading.value) {
    ElMessage.warning('正在提交中，请稍候...');
    return;
  }
  done();
}

// 配置树形结构的字段
const treeProps = {
  label: 'title',
  children: 'children'
}
// 定义 emit 事件
const emit = defineEmits<{
  (event: 'refresh'): void;
}>();
// 打开弹窗并填充数据
const open = (data = {}) => {
  console.log("接收参数", data)

  // 重置表单数据
  form.value = {
    id: 0,
    title: '',
    rule: '',
    parent_id: '',
    is_menu: 0,
    weight: 0,
    status: 1,
    remark: '',
    path: '',
    icon: ''
  };

  // 合并传入的数据
  if (data && Object.keys(data).length > 0) {
    const merged = { ...data } as any;
    
    // 处理parent_id为0的情况
    if (merged.parent_id === 0) {
      merged.parent_id = '';
    }
    
    // 确保数值类型正确
    if (merged.is_menu !== undefined) {
      merged.is_menu = Number(merged.is_menu);
      console.log('设置is_menu:', merged.is_menu, typeof merged.is_menu);
    } else {
      // 如果没有传入is_menu，确保默认值为0
      merged.is_menu = 0;
    }
    if (merged.status !== undefined) {
      merged.status = Number(merged.status);
    }
    if (merged.weight !== undefined) {
      merged.weight = Number(merged.weight);
    }
    
    Object.assign(form.value, merged);
    console.log('合并后的表单数据:', form.value);
  }

  // 获取权限树数据
  GetUserRule();
  
  visible.value = true;
  
  // 强制更新DOM
  nextTick(() => {
    console.log('DOM更新后的表单数据:', form.value);
    // 强制触发响应式更新
    form.value = { ...form.value };
    // 更新radio key强制重新渲染
    radioKey.value++;
  });
};
// 提交表单
const handleSubmit = async () => {
  if (!formRef.value) return;
  
  try {
    // 表单验证
    await (formRef.value as any).validate();
    
    loading.value = true;
    
    let data;
    if (form.value.id == 0) {
      data = await addRule(form.value);
    } else {
      data = await editRule(form.value);
    }

    if (data.status) {
      ElMessage.success((data as any).message);
      emit('refresh');
      visible.value = false;
    } else {
      ElMessage.error((data as any).message || '操作失败');
    }
  } catch (error) {
    console.error('提交失败:', error);
    ElMessage.error('提交失败，请检查表单信息');
  } finally {
    loading.value = false;
  }
}
// 暴露 `open` 方法，供外部调用
defineExpose({ open });
</script>

<template>
  <el-dialog 
    v-model="visible" 
    :title="form.id ? '编辑权限规则' : '添加权限规则'" 
    width="60%"
    :close-on-click-modal="false"
    class="rule-dialog"
    :before-close="handleClose"
  >
    <el-form 
      :model="form" 
      label-width="120px" 
      :rules="rules"
      ref="formRef"
      class="rule-form"
    >
      <!-- 基本信息 -->
      <div class="form-section">
        <div class="section-title">
          <i class="el-icon-document"></i>
          <span>基本信息</span>
        </div>
        <el-row :gutter="20">
          <!-- 权限名称 -->
          <el-col :span="12">
            <el-form-item label="权限名称" prop="title">
              <el-input 
                v-model="form.title" 
                placeholder="请输入权限名称"
                clearable
                prefix-icon="el-icon-edit"
              />
            </el-form-item>
          </el-col>

          <!-- 权限标识 -->
          <el-col :span="12">
            <el-form-item label="权限标识" prop="rule">
              <el-input 
                v-model="form.rule" 
                placeholder="如：user:create"
                clearable
                prefix-icon="el-icon-key"
              />
            </el-form-item>
          </el-col>
        </el-row>
      </div>

      <!-- 权限配置 -->
      <div class="form-section">
        <div class="section-title">
          <i class="el-icon-setting"></i>
          <span>权限配置</span>
        </div>
        <el-row :gutter="20">
          <!-- 上级权限 -->
          <el-col :span="12">
            <el-form-item label="上级权限" prop="parent_id">
              <el-tree-select
                v-model="form.parent_id"
                :data="treeData"
                :props="treeProps"
                node-key="id"
                check-strictly
                default-expand-all
                placeholder="请选择上级权限"
                clearable
                filterable
                style="width: 100%"
              />
            </el-form-item>
          </el-col>

          <!-- 是否菜单 -->
          <el-col :span="12">
            <el-form-item label="是否菜单" prop="is_menu">
              <el-radio-group v-model="isMenuValue" :key="radioKey">
                <el-radio :value="1" class="menu-radio">是</el-radio>
                <el-radio :value="0" class="menu-radio">否</el-radio>
              </el-radio-group>
              <div style="margin-top: 5px; font-size: 12px; color: #999;">
                调试: {{ form.is_menu }} ({{ typeof form.is_menu }})
              </div>
            </el-form-item>
          </el-col>
        </el-row>
      </div>

      <!-- 状态配置 -->
      <div class="form-section">
        <div class="section-title">
          <i class="el-icon-switch-button"></i>
          <span>状态配置</span>
        </div>
        <el-row :gutter="20">
          <!-- 状态 -->
          <el-col :span="12">
            <el-form-item label="状态" prop="status">
              <el-switch 
                v-model="form.status" 
                :active-value="1" 
                :inactive-value="0"
                active-text="启用"
                inactive-text="禁用"
              />
            </el-form-item>
          </el-col>

          <!-- 权重 -->
          <el-col :span="12">
            <el-form-item label="权重" prop="weight">
              <el-input-number 
                v-model="form.weight" 
                :min="0" 
                :max="999"
                style="width: 100%"
                controls-position="right"
              />
            </el-form-item>
          </el-col>
        </el-row>
      </div>

      <!-- 菜单配置（仅菜单显示） -->
      <div class="form-section" v-if="form.is_menu === 1">
        <div class="section-title">
          <i class="el-icon-menu"></i>
          <span>菜单配置</span>
        </div>
        <el-row :gutter="20">
          <!-- 前端路径 -->
          <el-col :span="24">
            <el-form-item label="前端路径" prop="path">
              <el-input 
                v-model="form.path" 
                placeholder="如：/user/list"
                clearable
                prefix-icon="el-icon-link"
              />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <!-- 图标 -->
          <el-col :span="24">
            <el-form-item label="显示图标" prop="icon">
              <el-input
                v-model="form.icon"
                placeholder="搜索图标，如：fa fa-home"
                clearable
              >
                <template #prepend>
                  <i :class="form.icon || 'fa fa-circle-o'"></i>
                </template>
                <template #append>
                  <el-button :icon="Search" @click="showIconHelp" />
                </template>
              </el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </div>

      <!-- 其他信息 -->
      <div class="form-section">
        <div class="section-title">
          <i class="el-icon-edit-outline"></i>
          <span>其他信息</span>
        </div>
        <el-row :gutter="20">
          <el-col :span="24">
            <el-form-item label="备注" prop="remark">
              <el-input 
                v-model="form.remark" 
                type="textarea" 
                :rows="3"
                placeholder="请输入备注信息"
                maxlength="200"
                show-word-limit
              />
            </el-form-item>
          </el-col>
        </el-row>
      </div>
    </el-form>

    <template #footer>
      <div class="dialog-footer">
        <el-button @click="visible = false">取消</el-button>
        <el-button type="primary" @click="handleSubmit" :loading="loading">
          {{ form.id ? '更新' : '创建' }}
        </el-button>
      </div>
    </template>
  </el-dialog>
</template>

